* {
    box-sizing: border-box;
}
.Nav {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-top: 12px;
    height: 12vh;
}
.medicare {
    color: rgb(16, 151, 204);
    /* padding-left: 8vw; */
    font-weight: bold;
    font-size: xx-large;
}
.medicare span {
    color: orange;
}
.navMenu {
    display: flex;
    justify-content: space-between;
}
.p18 {
    padding-right: 28px;
    color: rgb(16, 151, 204);
    font-weight: 700;
}
.navBtn {
    background-color: rgb(16, 151, 204);
    color: white;
    border: none;
    border-radius: 35px;
    width: 100px;
    padding-bottom: 4px;
    height: 5vh;
    font-weight: bold;
}
.navBtn:hover {
    border: 2px solid rgb(16, 151, 204);
    color: rgb(16, 151, 204);
    background-color: rgb(232, 230, 230);
    border-radius: 30px;
    font-weight: bold;
}