.welcome {
    display: flex;
}
.welcomeText {
    width: 55%;
    margin-top: 140px;
    margin-right: 100px;

}
.welcomeBox {
    background: #EBEEFF;
    border-radius: 32px;
    width: 574px;
    height: 672px;
}
.welcomeText h4 {
    font-weight: 700;
}
.welcomeText h1 {
    color: darkblue;
    font-size: 8vh;
    font-weight: bolder;
}
.welcomeText p {
    color: rgb(76, 74, 74);
}
.sec1Btn {
    display: flex;
    justify-content: space-between;
    /* margin-top: 90px; */
}
.welcomeBtn {
    border: none;
    background-color: rgb(16, 151, 204);
    width: 300px;
    height: 7vh;
    border-radius: 30px;
    color: white;
    font-weight: bold;
}
