.section3 {
    display: flex;
    margin-top: 200px;
}
.section3btn {
    border: none;
    background-color: rgb(16, 151, 204);
    width: 200px;
    height: 7vh;
    border-radius: 30px;
    color: white;
    font-weight: bold;
}
.section3btn:hover {
    border: 2px solid rgb(16, 151, 204);
    color: rgb(16, 151, 204);
    background-color: rgb(232, 230, 230);
    border-radius: 30px;
    font-weight: bold;
}
.section3Box {
    width: 200px;
    height: 150px;
    box-shadow: 5px 10px 22px -5px #888888;
    border-radius: 16px;
}
.section3Box img {
    width: 100%;
    height: 100%;
    margin-top: -80px;
}
