.styleHeading {
    color: darkblue;
    font-weight: 700;
}
.customerBoxes {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}
.customerBox {
    width: 45%;
    height: 40vh;
    box-shadow: 5px 10px 22px -5px #888888;
    border-radius: 12px;
    margin-top: 18px;
}
.review {
    padding: 50px 65px 0 65px;
    font-size: small;
}
.customer {
    display: flex;
    justify-content: space-between;
    padding-left: 65px;
    width: 60%;
    align-items: center;
}
.pic {
    width: 100px;
    /* border: 2px solid black; */
    border-radius: 50%;
    height: 100px;
}
.pic img {
    width: 100px;
    height: 100px;
    border-radius: 50%;
}