.bgBlue {
    background-color: rgb(16, 151, 204);
    height: 460px;
    border-radius: 16px;
    margin-top: 130px;
    text-align: center;
}
.bgBlue h1 {
    color: white;
    padding-top: 55px;
}
.bgBlue p {
    color: white;
}
.inputField {
    border: none;
    border-radius: 8px;
    width: 60%;
    height: 15vh;
    margin-top: 65px;
    padding-left: 65px;
}
.send {
    background-color: rgb(16, 151, 204);
    width: 150px;
    height: 8vh;
    border-radius: 36px;
    border: none;
    margin-left: -200px;
    color: white;
    font-weight: bold;
}
.send:hover {
    border: 2px solid rgb(16, 151, 204);
    color: rgb(16, 151, 204);
    background-color: rgb(232, 230, 230);
    border-radius: 30px;
    font-weight: bold;
}